import uniListItem from "@/components/uni-ui/uni-list-item/uni-list-item.vue"
import price from "@/components/common/price.vue"

import {mapGetters,mapState} from "vuex"
export default {
	components: {
		uniListItem,
		price
	},
	data() {
		return {
			loading:false,
			path:false,
			items:[],
			couponCount:0,
			coupon:{
				id:0,
				type:0,
				value:0
			},
			order_id:0
		}
	},
	computed: {
		...mapState({
			list:state=>state.cart.list
		}),
		...mapGetters([
			'defaultPath',
			'totalPrice'
		]),
		// 商品列表
		goodsList(){
			return this.items.map(id=>{
				return this.list.find(v=>v.id === id)
			})
		},
		// 最终价格
		price(){
			// 没有优惠券
			if(this.coupon.id === 0){
				return this.totalPrice
			}
			if(this.coupon.type === 0){
				return this.totalPrice - this.coupon.value
			}
			return (this.totalPrice * (this.coupon.value/10)).toFixed(2)
		}
	},
	onLoad(e) {
		if(!e.detail){ 
			uni.showToast({
				title: '请选择要下单的商品',
				icon: 'none'
			});
			return uni.navigateBack({
				delta: 1
			});
		}
		this.items = JSON.parse(e.detail)
		
		if (this.defaultPath.length) {
			this.path = this.defaultPath[0]
		}
		// 开启监听选择收货地址事件
		uni.$on('choosePath',(res)=>{
			this.path = res
		})
		// 监听选择优惠券
		uni.$on('couponUser',(res)=>{
			this.coupon = res
		})
		// 计算当前价格有多少张可用优惠券
		this.getCouponCount()
	},
	onUnload() {
		// 关闭监听选择收货地址事件
		uni.$off('choosePath')
		uni.$off('couponUser')
	},
	onShow() {
		// 如果已经提交过订单了，重定向到订单详情页
		if(this.order_id > 0){
			uni.redirectTo({
				url: '../order-detail/order-detail?id='+this.order_id,
			});
		}
	},
	methods: {
		openPathList(){
			uni.navigateTo({
				url: '../user-path-list/user-path-list?type=choose'
			});
		},
		openOrderInvoice(){
			uni.navigateTo({
				url: '../order-invoice/order-invoice'
			});
		},
		// 下单，支付
		openPayMethods(){
			// 防止重复下单
			if(this.loading) return;
			// 判断是否选择收货地址
			if(!this.path){
				return uni.showToast({
					title: '请选择收货地址',
					icon: 'none'
				});
			}
			let options = {
				user_addresses_id:this.path.id,
				items:this.items.join(',')
			}
			// 是否选择优惠券
			if(this.coupon.id > 0){
				options.coupon_user_id = this.coupon.id
			}
			this.loading = true
			this.$H.post('/order',options,{
				token:true
			}).then(res=>{
				this.loading = false
				// 跳转到支付页面
				uni.navigateTo({
					url: '../pay-methods/pay-methods?detail='+JSON.stringify({
						id:res.id,
						price:res.total_price
					})
				});
				// 保存订单id
				this.order_id = res.id
				// 通知购物车更新数据
				uni.$emit('updateCart')
			}).catch(err=>{
				this.loading = false
				console.log(err);
				uni.showToast({
					title: '创建订单失败',
					icon: 'none'
				});
			})
		},
		// 选择优惠券
		openCoupon(){
			uni.navigateTo({
				url: '../order-coupon/order-coupon?detail='+JSON.stringify({
					price:this.totalPrice
				}),
			});
		},
		// 计算当前价格有多少张可用优惠券
		getCouponCount(){
			this.$H.post('/coupon_count',{
				price:this.totalPrice
			},{
				token:true
			}).then(res=>{
				this.couponCount = res
			}).catch(err=>{
				uni.showToast({
					title: '获取可用优惠券数量失败',
					icon: 'none'
				});
			})
		}
	}
}
